import Loop from 'raf-loop';
import { saveAs } from 'file-saver';

class BaseEngine {
  constructor(defaultOptions, options) {
    this.options = {
      ...defaultOptions,
      ...options
    };

    this.frame = 0;

    this._renderer = () => {};
  }

  _addEventListeners() {
    this.wrapper.addEventListener('click', () => {
      this.options.debug = !this.options.debug;

      document.body.classList.toggle('debug', this.options.debug);
    }, false);
  }

  _removeLoading() {
    const loading = document.getElementById('loading');
    loading.parentElement.removeChild(loading);
  }

  updateDimensions(width, height, pixelRatio, pixelate = 1) {
    this.wrapper.style.width = `${width * pixelate}px`;
    this.wrapper.style.height = `${height * pixelate}px`;

    if (this.canvasEl) {
      this.canvasEl.width = width * pixelRatio;
      this.canvasEl.height = height * pixelRatio;

      this.canvasCtx.scale(pixelRatio, pixelRatio);
    }

    if (this.renderer) {
      this.renderer.setSize(width, height);
      this.renderer.setPixelRatio(pixelRatio);
    }
  }

  onRender(fn = () => {}) {
    this._renderer = fn;
  }

  preload(cb = () => {}) {
    return new Promise((resolve, reject) => {
      cb(resolve);
    });
  }

  start() {
    this.loop = Loop(this.update.bind(this));

    this._addEventListeners();
    this._removeLoading();

    this.loop.start();
  }

  stop() {
    this.loop.stop();
  }

  saveFrame(el = document.querySelector('canvas')) {
    const d = new Date();

    const dStr = [
      d.getFullYear(),
      (d.getMonth() + 1).toString().padStart(2, '0'),
      d.getDate().toString().padStart(2, '0'),
    ].join('-');


    el.toBlob((blob) => {
        saveAs(blob, `${ dStr }-${ this.frame }.png`);
    }, 'image/png');
  }
}

export default BaseEngine;
